import { Pipe, PipeTransform } from '@angular/core';
import { truncate } from "../common/helper";

/**
 * 
 */
@Pipe( {
    name: 'truncate'
} )
export class TruncatePipe implements PipeTransform {
    /**
     * 
     * @param value 
     * @param length ,abcd,2 ab...
     */
    transform( value, length ) {
        return truncate( value, length );
    }
}