import { Observable } from 'rxjs';
import { NzMessageService, NzModalService } from 'ng-zorro-antd';
import { IocHelper as ioc } from '../angular/ioc-helper';

/**
 * 
 */
export class Message {
    /**
     * 
     * @param message 
     */
    static success( message: string ): void {
        let service = ioc.injector.get( NzMessageService );
        service.success( message );
    }

    /**
     * 
     * @param message 
     */
    static info( message: string ): void {
        let service = ioc.injector.get( NzMessageService );
        service.info( message );
    }

    /**
     * 
     * @param message 
     */
    static warn( message: string ): void {
        let service = ioc.injector.get( NzMessageService );
        service.warning( message );
    }

    /**
     * 
     * @param message 
     */
    static error( message: string ): void {
        let service = ioc.injector.get( NzMessageService );
        service.error( message );
    }

    /**
     * 
     * @param options 
     */
    static confirm( options: {
        /**
         * 
         */
        message: string,
        /**
         * 
         */
        ok: () => void,
        /**
         * 
         */
        cancel?: () => void,
        /**
         * 
         */
        title?: string;
    } ): void;
    /**
     * 
     * @param message 
     * @param ok 
     */
    static confirm( message: string, ok?: () => void ): void;
    static confirm( options, ok?): void {
        let message = "";
        let title = "";
        let cancel: () => void;
        if ( typeof options === "object" ) {
            message = options["message"];
            title = options["title"];
            ok = options["ok"];
            cancel = options["cancel"];
        }
        else if ( typeof options === "string" ) {
            message = options;
        }
        let service = ioc.injector.get( NzModalService );
        service.confirm( {
            nzTitle: title || "",
            nzContent: message,
            nzOnOk: ok,
            nzOnCancel: cancel
        } );
    }
}