import { Pipe, PipeTransform } from '@angular/core';

/**
 * 
 */
@Pipe( {
    name: 'isTruncate'
} )
export class IsTruncatePipe implements PipeTransform {
    /**
     * 
     * @param value 
     * @param length 
     */
    transform( value, length ) {
        if ( !value )
            return false;
        return value.length > length;
    }
}