import { Injectable } from '@angular/core';
import { uuid } from '../common/helper';

/**
 * 
 */
@Injectable()
export class Session {
    /**
     * 
     */
    constructor() {
        this.sessionId = uuid();
    }
    /**
     * 
     */
    sessionId;
    /**
     * 
     */
    isAuthenticated;
    /**
     * 
     */
    userId;
    /**
     * 
     */
    name;
}