/**
 * 
 */
export interface ISort {
    /**
     * 
     */
    sortId?: number;
}

/**
 * 
 * @param array 
 */
export function sort<T extends ISort>(array: T[]): T[] {
    return array.sort((a, b) => (a.sortId === undefined || b.sortId === undefined) ? 0 : a.sortId - b.sortId);
}