/**
 * OpenId Connect
 */
export class AuthorizeConfig {
    /**
     * OpenId Connect
     */
    authority: string;
    /**
     * 
     */
    clientId: string;
    /**
     * ,: openid profile
     */
    scope: string = "openid profile";
    /**
     * id_token token
     */
    responseType: string = "id_token token";
    /**
     * /callback:OpenId Connect
     */
    redirectUri: string = "/callback";
    /**
     * /:OpenId Connect
     */
    postLogoutRedirectUri: string;

    /**
     * 
     */
    validate() {
        if (!this.authority)
            throw new Error("OpenId ConnectAuthorizeConfigauthority");
        if (!this.clientId)
            throw new Error("OpenId ConnectAuthorizeConfigclientId");
    }

    /**
     * 
     */
    getRedirectUri() {
        if (this.redirectUri === "/callback")
            return `${location.origin}${this.redirectUri}`;
        return this.redirectUri;
    }

    /**
     * 
     */
    getPostLogoutRedirectUri() {
        if (!this.postLogoutRedirectUri)
            return location.origin;
        return this.postLogoutRedirectUri;
    }
}